<template>
  <div class="md:tw-col-span-3 tw-col-span-12 tw-rounded">
    <label
      for="countries"
      class="tw-block tw-mb-2 tw-text-sm tw-font-medium tw-text-gray-900"
      >Product</label
    >

    <select
      @change="
        (e) => $emit('update', { ...filters, product_id: e.target.value })
      "
      :value="filters.product_id"
      class="tw-bg-gray-50 tw-border-solid tw-outline-none tw-border tw-border-gray-300 tw-text-gray-900 tw-text-sm tw-rounded-lg focus:tw-ring-orange-500 focus:tw-border-orange-500 tw-block tw-w-full tw-p-2.5"
    >
      <option value="all">All</option>
      <option
        :value="p.id"
        class="tw-capitalize"
        v-for="p in products"
        :key="p.id"
      >
        {{ p.name }}
      </option>
    </select>
  </div>
</template>

<script>
import Product from '@/api/Product'

export default {

    props: {
        filters: {
            required: true,
        }
    },

    data() {
        return {
        }
    },

    computed: {
        products() {
            return this.$store.getters['product/products']
        },
        fetched() {
            return this.$store.getters['product/fetched']
        },
    },

    methods: {
        getProducts() {
            return Product.all().then(
            (res) => {
                if (res.data.code == "SUCCESS") {
                    this.$store.dispatch("product/setProducts", res.data.data.products);
                    this.$store.dispatch("product/setFetched", true);
                }
                },
                (err) => {
                this.$handleApiError(err);
                }
            );
        }
    }
}
</script>

